'use strict';

import moment from "moment";
import ITDForm from "@/components/ITDForm";
//TODO transformar los validators por:
//  {
//      [validador]:{
//          [tipo o tipo de dato](){}
//          text(){},
//          string(){},
//          default(){}
//      }
//  }
//  cambiar addValidator para usar este modelo y advertir en modo estricto si se sobre escribe.
//  invalidFeedback debe intentar usar la funcin del tipo, luego la del tipo del valor de lo contrario usar default.
//  si est en modo estricto debe generar warning si cae en default.
//  Se debe tener mucho cuidado que cualquier cosa que no sea Array llega como string, por lo tanto se debe 
//  verificar con isNaN para ver si es nmero y con Array.isArray si es arreglo.
//  Boolean a lo ms debera tener validacin de required, en caso de radiobuttons. para uno solo no hay como verificarlo.
//  Se debe probar si se pueden replicar las funciones con return this.func, teniendo en cuenta que se cambia el this de la funcin. para eso sera bueno tener una funcin de replicacin como el addValidator y un ltimo campo con el nombre de la funcin a replicar.

const validators={
    min:{
        default(min){//aplica para string
            return !this.value || this.value.length<min?`"${this.input.label}" debe tener un largo mnimo de ${min}`:"";
        },
        number(min){
            return this.value<min?`el valor debe ser mayor a ${min}`:"";
        },
        array(min){
            return this.value.length<min?`el valor debe tener como mnimo ${min} campos seleccionaos`:"";
        },
        date(min){
            return moment(this.value).isBefore(min)?`La fecha debe ser mayor que ${moment(min).format("DD/MM/YYYY")}`:"";
        }
    },
    max:{
        default(max){
            return this.value && this.value.length>max?`el valor debe tener un largo mximo de ${max}`:"";
        },
        number(max){
            return this.value>max?`el valor debe ser menor a ${max}`:"";
        },
        array(max){
            return this.value.length>max?`el valor debe tener como mximo ${max} campos seleccionaos`:"";
        },
        date(max){
            return moment(this.value).isAfter(max)?`La fecha debe ser a lo ms ${moment(max).format("DD/MM/YYYY")}`:"";
        }
    },
    range:{
        default(min,max){
            return (this.value<min||this.value>max)?`el valor debe tener un largo entre ${min} y ${max}`:"";
        },
        number(min,max){
            return (this.value<min||this.value>max)?`el valor debe ser entre ${min} y ${max}`:"";
        },
        array(min,max){
            console.log(min,max,this.value);
            return (this.value.length<min||this.value.length>max)?`el valor debe tener entre ${min} y ${max} campos seleccionaos`:"";
        },
        date(min,max){
            return !moment(this.value).isBetween(min,max,null,"[]")?`La fecha debe ser entre ${moment(min).format("DD/MM/YYYY")} y ${moment(max).format("DD/MM/YYYY")}`:"";
        }
    },
    regexp:{
        string(...args){
            return !RegExp.apply(RegExp,args).test(this.value)?`El valor debe cumplir la expresn regular /${args[0]}/${args[1]?args[1]:''}`:"";
        },
        default(){
            let {type} = this.input;
            throw new Error(`No existe validador regexp para el tipo ${type} usada en ${this.input.name}`);
        }
    },
    double_check:{
        array(){
            let {value,double_check} = this;
            let Dvalue = double_check;
            let result;
            if(Array.isArray(value)){
                result= value.length===Dvalue.length&&value.reduce((test,val,pos)=>test&&val===Dvalue[pos],true);
            }
            return !result?"Los valores no coinciden":"";
        },
        default(){
            let {value,double_check} = this;
            let Dvalue = double_check;
            let result=value===Dvalue;
            return !result?"Los valores no coinciden":"";
        }
    }
};
//cast de validadores por tipo
const cast = {
    text:"string",
    number:"number",
    range:"number"
};
function getType(input){
    let val = input.value;
    //casting por input
    if(input.type in cast){
        console.log("cast para",name,cast[input.type]);
        return cast[input.type];
    } 
    //no hay cast pasamos a adivinar
    if(Array.isArray(val)) return "array";
    if(!isNaN(val)) return "number";
    if(moment(val).isValid()) return "date";
    if(typeof val === "string") return "string";
    return "default";
}
function getValidator(func,input,modifiers){
    //console.log("getValidator",func,name);
    let type = getType(input);
    //console.log(val,type);
    if(validators[func]){
        let v = validators[func];
        if(v[type]) return v[type];
        if(v.default) return v.default;
        else{
            console.warn(`no existe el tipo ${type} ni default para el validador ${func}`);
            if(modifiers.strict) throw new Error(`no existe el tipo ${type} ni default para el validador ${func}`);
            return ()=>"";
        }
    }else{
        console.warn(`no existe el validador ${func}`);
        if(modifiers.strict) throw new Error(`no existe el validador ${func}`);
        return ()=>"";
    }
}
function addValidator(validator,type,func){
    console.log("addValidator");
    console.log(validator,type,func);
    console.log(this);
    if(this.validators[validator]){//ya existe el validador, lo sobrecargamos.
        console.log("ya existe",validator);
        if(this.validators[validator][type]){
            console.warn("ya existe",validator,type,"sobreescribiendo");
        }
        this.validator[validator][type]=func;
    }else{
        console.log("validador nuevo",validator);
        this.validators[validator]={[type]:func};
    }
    console.log(this);
}
function double_check(input){
    if(input.validate && "double_check" in input.validate){
        input.double_check = input.value||(input.multiple?[]:"");
    }
}
function double_checkAll(inputs){
    inputs.forEach(double_check);
}
function copy_value(input){
    if(Array.isArray(input.value) || typeof input.value==="object") input.originalValue = JSON.parse(JSON.stringify(input.value));
    else input.originalValue = input.value;
}
function copy_original(input){
    if(Array.isArray(input.originalValue) || typeof input.originalValue==="object") input.value = JSON.parse(JSON.stringify(input.originalValue));
    else input.value = input.originalValue;
}
function copy_originalAll(inputs){
    inputs.forEach(copy_original);
}
const getValues = (inputs)=>inputs.reduce((obj,el)=>Object.assign(obj,{[el.name]:el.value}),{});
const clearForm = (inputs)=>inputs.forEach(input=>input.value=input.originalValue);
const resetForm = (inputs)=>inputs.forEach(input=>input.value=Array.isArray(input.value)?[]:null);
let funcs = {
    clearForm,
    resetForm,
    validateOne,
    addValidator,
    double_checkAll,
    copy_original,
    copy_originalAll,
    getValues
};
function validateOne(input,value,double_check,modifiers){
    console.log("validateOne");
    console.log(input.name);
    console.log(input.validate);
    console.log(value);
    let {validate={}} = input;
    let TXT=Object.keys(validate).reduce((txt,func,pos,arr)=>{
        let result;
        let v = getValidator(func,input,modifiers);
        result =v[Array.isArray(validate[func])?"apply":"call"]({value,input,double_check,validators,modifiers},validate[func]);
        txt+=result?result+(pos<arr.length-1?", ":""):"";
        console.log("validando",func,txt);
        return txt;
    },"");
    
    return TXT;

}
function validate(el,{context:vm},value,modifiers){
    //validamos...
    let {double_check} = vm;
    console.log("validate");
    console.log(el);
    console.log("inputs");
    vm.form.forEach(i=>{
        console.log(i);
        console.log(value);
        console.log(value[i.name]);
        let error = validateOne(i,value[i.name],double_check[i.name],modifiers);
        let state = error.length===0;
        console.log(state,error);
        i.error=error;
        i.state=state;
        if(modifiers.native){
            console.log("configuracin nativa, buscando inputs");
            let input=[];
            let inputs= ["input","select","textarea"];
            //buscamos nodetype y comparamos
            if(inputs.indexOf(el.nodeName.toLowerCase())!==-1){
                console.log("el elemento es un input");
                input=[el];
            }else{
                //buscamos dentro del elemento. 
                input = el.querySelectorAll(inputs.join(","));
                if(input.length<1) console.warn("no existen inputs en el tag para definir el validador personalizado");
                else{
                    console.log("inputs encontrados");
                    console.log(input);
                }
            }
            input.forEach(el=>el.setCustomValidity(error));
        }
    });
}
export default {
    install(Vue,options){
        options = options || {};
        Vue.$validator = funcs;
        Vue.component("ITDForm",ITDForm);
        Vue.directive("validate", {
            bind(el,binding,vnode){
                let {modifiers} = binding;
                let {context:vm} = vnode;
                let value = vm[vnode.data.model.expression];
                console.log("v-validate bind");
                console.log(vnode);
                console.log(el);
                console.log(binding);
                console.log(vnode);
                console.log(vm);
                console.log(value);
                validate(el,vnode,value,modifiers);

                vnode.context.$on("input", function(val){
                    console.log("$on por validar a",val);
                    validate(el,vnode,val,modifiers);
                });
            }
        });
    }
};



